"use client"

import { useRef, useMemo } from "react"
import { Canvas, useFrame } from "@react-three/fiber"
import { Environment, Instance, Instances } from "@react-three/drei"
import * as THREE from "three"

const COUNT = 30
const SPEED_FACTOR = 2

function Coin({ position, speed, rotationSpeed }: { position: [number, number, number], speed: number, rotationSpeed: [number, number, number] }) {
    const ref = useRef<THREE.Group>(null)

    useFrame((state, delta) => {
        if (!ref.current) return

        // Fall down
        ref.current.position.y -= speed * delta * SPEED_FACTOR

        // Reset position when out of view
        if (ref.current.position.y < -10) {
            ref.current.position.y = 10
        }

        // Rotate
        ref.current.rotation.x += rotationSpeed[0] * delta
        ref.current.rotation.y += rotationSpeed[1] * delta
        ref.current.rotation.z += rotationSpeed[2] * delta
    })

    return (
        <group ref={ref} position={position}>
            <Instance />
        </group>
    )
}

function Coins() {
    const data = useMemo(
        () =>
            Array.from({ length: COUNT }, () => ({
                position: [
                    (Math.random() - 0.5) * 20,
                    (Math.random() - 0.5) * 20,
                    (Math.random() - 0.5) * 5
                ] as [number, number, number],
                speed: 0.5 + Math.random(),
                rotationSpeed: [
                    Math.random(),
                    Math.random(),
                    Math.random()
                ] as [number, number, number]
            })),
        [],
    )

    return (
        <Instances range={COUNT}>
            <cylinderGeometry args={[0.5, 0.5, 0.1, 32]} />
            <meshStandardMaterial color="#fbbf24" metalness={0.8} roughness={0.2} />
            {data.map((props, i) => (
                <Coin key={i} {...props} />
            ))}
        </Instances>
    )
}

export function CoinsBackground() {
    return (
        <div className="fixed inset-0 z-0 pointer-events-none fade-in">
            <Canvas camera={{ position: [0, 0, 10], fov: 45 }}>
                <ambientLight intensity={0.8} />
                <pointLight position={[10, 10, 10]} intensity={1.5} />
                <Coins />
                <Environment preset="city" />
            </Canvas>
        </div>
    )
}
